<template>
  <div id="app-layout">
    <!-- ‚úÖ ÂÖ®Â±ÄÂ∏∏È©ª‰æßËæπÊ†è -->
    <aside class="sidebar">
      <h2 class="sidebar-title">üéµ ÂäüËÉΩÂØºËà™</h2>
      <button class="sidebar-btn" @click="$router.push('/')">Èü≥‰πêÊêúÁ¥¢</button>
      <button class="sidebar-btn" @click="$router.push('/favorite')">ÊàëÂñúÊ¨¢ÁöÑÈü≥‰πêÊ≠åÂçï</button>
    </aside>

    <!-- ‚úÖ ‰∏ª‰ΩìÂå∫ÂüüÔºàÊâÄÊúâÈ°µÈù¢ÂÜÖÂÆπÔºâ -->
    <main class="main-content">
      <router-view />
    </main>

    <!-- ‚úÖ ÂÖ®Â±ÄÊí≠ÊîæÂô® -->
    <GlobalAudioPlayer
      v-if="player"
      :src="player.url"
      :title="player.song"
      :artist="player.singer"
      :cover="player.cover"
      :musicId="player.id"
      :playMode="playMode"
      @togglePlayMode="togglePlayMode"
      @ended="playNext"
      @timeupdate="handleTimeUpdate"
      @prev="playPrev"
      @next="playNext"
    />
  </div>
</template>
<script>
import GlobalAudioPlayer from './components/GlobalAudioPlayer.vue';
import { searchMusicByIdVkeys } from './api/music';

export default {
  name: 'App',
  components: { GlobalAudioPlayer },
  data() {
    return {
      player: {
        url: '',
        song: '',
        singer: '',
        cover: '',
        id: '',
        playIndex: 0,
        playList: [],
        currentTime: 0
      },
      playMode: 'order', // ‚úÖ Êñ∞Â¢ûÔºöÊí≠ÊîæÊ®°ÂºèÔºàorder/random/singleÔºâ
      playedSet: new Set() // ‚úÖ ÈöèÊú∫Ê®°Âºè‰∏ãËÆ∞ÂΩïÂ∑≤Êí≠ÊîæÁöÑÊ≠åÊõ≤
    };
  },
  methods: {
    handleTimeUpdate(currentTime) {
      if (this.player) this.player.currentTime = currentTime;
    },

    togglePlayMode() {
      const modes = ['order', 'random', 'single'];
      const next = modes[(modes.indexOf(this.playMode) + 1) % modes.length];
      this.playMode = next;
      this.playedSet.clear(); // ÂàáÊç¢Ê®°ÂºèÊó∂Ê∏ÖÁ©∫ÂéÜÂè≤
      console.log('Êí≠ÊîæÊ®°ÂºèÂàáÊç¢‰∏∫Ôºö', next);
    },

    async retryFetchMusicUrl(id, maxRetries = 10, delay = 800) {
      for (let attempt = 1; attempt <= maxRetries; attempt++) {
        try {
          const res = await searchMusicByIdVkeys(id);
          if (res.data?.code === 200 && res.data.data.url) return res.data.data.url;
        } catch (error) {
          console.error(`Â∞ùËØïËé∑ÂèñÈü≥‰πê URL Â§±Ë¥•ÔºàÁ¨¨ ${attempt} Ê¨°ÔºâÔºö`, error);
        }
        await new Promise(r => setTimeout(r, delay));
      }
      return null;
    },

    /** Êí≠Êîæ‰∏ä‰∏ÄÈ¶ñÔºàÈ°∫Â∫èÊ®°Âºè‰∏ãÔºâ */
    async playPrev() {
      if (this.playMode === 'random') return this.playRandom(); // ÈöèÊú∫Ê®°ÂºèÔºöÁõ¥Êé•ÈöèÊú∫
      const player = this.player;
      const prevIndex = player.playIndex - 1;
      if (prevIndex >= 0) {
        await this.playByIndex(prevIndex);
      } else {
        console.log('Â∑≤ÁªèÊòØÁ¨¨‰∏ÄÈ¶ñ‰∫Ü');
      }
    },

    /* Êí≠Êîæ‰∏ã‰∏ÄÈ¶ñ */
async playNext() {
  console.log('ÂΩìÂâçÊ®°ÂºèÔºö', this.playMode);
  if (this.playMode === 'random') {
    return this.playRandom();
  }

  // È°∫Â∫èÊí≠Êîæ
  const nextIndex = this.player.playIndex + 1;
  if (nextIndex < this.player.playList.length) {
    await this.playByIndex(nextIndex);
  } else {
    console.log('Êí≠ÊîæÂÆåÊúÄÂêé‰∏ÄÈ¶ñ');
    this.$root.player = null;
  }
}
,

    /** ÈöèÊú∫Êí≠ÊîæÔºà‰∏ç‰ºöÈáçÂ§çÔºâ */
    async playRandom() {
      const { playList, playIndex } = this.player;
      if (!playList.length) return;

      // Ê∑ªÂä†ÂΩìÂâçÊõ≤ÁõÆÂà∞Â∑≤Êí≠ÊîæÈõÜÂêà
      this.playedSet.add(playList[playIndex]?.id);

      const unplayed = playList.filter(item => !this.playedSet.has(item.id));
      if (unplayed.length === 0) {
        this.playedSet.clear(); // ÂÖ®ÈÉ®Êí≠ÊîæÂÆåÔºåÈáçÁΩÆ
        this.playedSet.add(playList[playIndex]?.id);
        console.log('üé≤ ÊâÄÊúâÊ≠åÊõ≤Â∑≤Êí≠Êîæ‰∏ÄËΩÆÔºåÈáçÊñ∞ÂºÄÂßãÈöèÊú∫');
      }

      const pool = playList.filter(item => !this.playedSet.has(item.id));
      const next = pool[Math.floor(Math.random() * pool.length)];
      const index = playList.findIndex(i => i.id === next.id);
      await this.playByIndex(index);
    },

    /** ÊåâÁ¥¢ÂºïÊí≠ÊîæÊ≠åÊõ≤ */
    async playByIndex(index) {
      const player = this.player;
      const nextItem = player.playList[index];
      const url = await this.retryFetchMusicUrl(nextItem.id);
      if (!url) return console.warn('Êó†Ê≥ïËé∑Âèñ URL');

      this.player = {
        ...player,
        url,
        song: nextItem.song,
        singer: nextItem.singer,
        cover: nextItem.cover,
        id: nextItem.id,
        playIndex: index
      };
    }
  }
};
</script>

<style>
/* =================== ÂÖ®Â±ÄÂ∏ÉÂ±Ä =================== */
#app-layout {
  display: flex;
  min-height: 100vh;
  background: linear-gradient(120deg, #eafaf3 0%, #fff 100%);
  color: #2c3e50;
  font-family: Avenir, Helvetica, Arial, sans-serif;
}

/* ‚úÖ ‰æßËæπÊ†èÊ†∑ÂºèÔºàÂ∏∏È©ªÔºâ */
.sidebar {
  width: 220px;
  background: linear-gradient(180deg, #42b983 0%, #369870 100%);
  color: #fff;
  padding: 40px 20px;
  box-shadow: 2px 0 8px rgba(66,185,131,0.1);
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
}

.sidebar-title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 30px;
  text-align: center;
}

.sidebar-btn {
  width: 100%;
  padding: 12px 0;
  background: #fff;
  color: #42b983;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  transition: background 0.2s, transform 0.2s;
  margin-bottom: 12px;
}

.sidebar-btn:hover {
  background: #eafaf3;
  transform: scale(1.05);
}

/* ‚úÖ ‰∏ª‰ΩìÂÜÖÂÆπ */
.main-content {
  margin-left: 240px;
  flex: 1;
  padding: 30px 20px;
  text-align: center;
  justify-content: center;
}

/* ‚úÖ ÂìçÂ∫îÂºèÈÄÇÈÖç */
@media (max-width: 768px) {
  #app-layout {
    flex-direction: column;
  }

  .sidebar {
    position: relative;
    width: 100%;
    height: auto;
    box-shadow: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
  }


}
</style>
