import axios from 'axios';
// ËêΩÊúàapi  https://doc.vkeys.cn/api-doc/v2/%E9%9F%B3%E4%B9%90%E6%A8%A1%E5%9D%97/%E7%BD%91%E6%98%93%E4%BA%91%E9%9F%B3%E4%B9%90/1-netease.html
const instance = axios.create({
  baseURL: 'https://api.vkeys.cn/v2/music/netease',
  timeout: 10000,
});


// vkeysÊé•Âè£ÔºöÈÄöËøáidÊü•ÊâæÈü≥‰πêËØ¶ÊÉÖÔºåÊîØÊåÅ quality ÂèÇÊï∞ÔºàÊï∞Â≠ó1-9Ôºâ
export function searchMusicByIdVkeys(id, quality = 4) {
  return instance.get('', { params: { id, quality } });
}

// ÊêúÁ¥¢ÂàóË°®Êé•Âè£Ôºà‰∏çÂèòÔºâ

export function searchMusic(word) {
  return instance.get('', { params: { word } });
}


// Ëé∑ÂèñÊ≠åËØçÊé•Âè£
export function fetchLyricById(id) {
  return instance.get('/lyric', { params: { id } });
}
